































import Vue from 'vue';
import FooterComponent from '@/components/FooterComponent.vue';

export default Vue.extend({
  name: 'Contact',
  components: {
    'vue-footer': FooterComponent,
  },
  beforeCreate: () => {
    const navCheck = document.querySelector('#nav-check') as HTMLFormElement;
    if (navCheck.checked === true) {
      navCheck.checked = false;
    }
  },
});
